body {
  background: var(--bg);
}

h1,
h2 {
  font-family: 'Barlow', sans-serif;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
}

h3,
h4 {
  font-family: 'Barlow', sans-serif;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
}

h5,
h6 {
  /* font-family: 'Roboto', sans-serif; */
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
}

a:not(.nav-link) {
  text-decoration: none !important;
  background-repeat: no-repeat;
  background-image: linear-gradient(180deg, transparent 65%, var(--highlight, rgba(251, 166, 105, 0.5)) 0);
  transition: background-size 0.25s ease;
  background-size: 0% 100%;
}

main a:not(.btn):not(.page-link):not(.nav-link):not(.dropdown-item):hover {
  background-size: 100% 100%;
  background-image: linear-gradient(180deg, transparent 65%, var(--highlight, rgba(251, 166, 105, 0.5)) 0);
}

body main {
  min-height: calc(100vh - 56px) !important;
}

input[type="range"] {
  padding: 0 !important;
}

.navbar {
  background-color: none;
  padding-top: calc(46px + .5rem);
  position: relative;
  z-index: 99;
  box-shadow: 0 1px 4px var(--shadow-light, rgba(159, 162, 177, 0.3));
}

.nav-item {
  position: relative;
}

main > div > .my-3 {
  padding: 1rem 0;
}

.navbar-brand {
  margin-right: 1rem;
}

.dropdown-menu {
  background-color: var(--white, #fff);
}

.dropdown-menu > a > i {
  width: 20px;
}

.dropdown-item {
  color: var(--body-color);
  margin: .2rem 0;
}

@media (max-width: 960px) {
  .nav-link.dropdown-toggle::after {
    float: right;
  }
  .nav-link.dropdown-toggle::after {
    transition: transform .3s ease-out;
  }
  .nav-link.dropdown-toggle[aria-expanded=true]::after {
    transform: rotate(-180deg);
  }
}

.card {
  box-shadow: 0 3px 8px var(--shadow-light, rgba(159, 162, 177, 0.3));
}

.card-header {
  background: none;
}

.card-body {
  padding: 1rem;
}

.table {
  margin-bottom: 0;
  width: 100% !important;
  line-height: 1.2;
  font-size: 0.93rem;
  white-space: nowrap !important;
}

.table td,
.table th {
  padding: 0.75rem;
  vertical-align: top;
  border-top: 1px solid var(--border-color, #e5e1e1);
  border-bottom: none;
}

.table thead th::before,
.table thead th::after {
  font-weight: 400;
}

.table thead th {
  color: var(--body-color);
  border-top: none;
  border-bottom: 1px solid var(--border-color, #e5e1e1);
  background-color: var(--bg-color-light, #e5e1e166);
  border-bottom-color: var(--border-color, #e5e1e1);
}

.dataTables_info,
.dataTables_length {
  padding-left: 1rem !important;
}

.dataTables_filter input {
  width: 200px !important;
}

.dataTables_paginate,
.dataTables_filter {
  padding-right: 1rem !important;
}

.nav-tabs .nav-item.show .nav-link,
.nav-tabs .nav-link.active {
  border-color: var(--border-color, #e5e1e1);
}

.nav-tabs .nav-link:focus,
.nav-tabs .nav-link:hover {
  border-color: var(--border-color, #e5e1e1);
}

.nav-tabs {
  border-bottom: 1px solid var(--border-color, #e5e1e1);
}

@media (max-width: 1140px) {
  .nav-indicator {
    display: none;
  }
  .navbar-nav {
    margin-top: 0.5rem;
    margin-left: 0.5rem;
  }
  .nav-item {
    margin: 0.2rem 0;
  }
}

.search-container {
  flex: 1 1 auto;
}

.search-container form {
  width: 100%;
  justify-content: flex-end;
  padding: 5px 0;
  position: relative;
}

.fa-search {
  position: absolute;
  color: grey;
  z-index: 99;
  right: 10px;
}

@media (min-width: 1140px) and (max-width: 1440px) {
  .navbar .nav-item i {
    display: none;
  }
  .navbar .nav-item svg {
    display: none;
  }
  .navbar-brand i {
    display: initial;
  }
}

.paginate_button {
  background-color: var(--bg-color-secondary, #fff);
}

.text-monospace {
  font-family: 'Roboto Mono', SFMono-Regular, Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace;
}

.form-check-input {
  border: 1px solid var(--input-border-color);
}

